import React from 'react';
import { graphql, Link } from 'gatsby';
import Layout from '../components/layout';

export default function Template({
  data // this prop will be injected by the GraphQL query below.
}) {
  const { markdownRemark } = data; // data.markdownRemark holds your post data
  const { excerpt, html, frontmatter } = markdownRemark;

  return (
    <Layout
      page='news'
      subtitle='お知らせ'
      subtitleEn='NEWS'
      breadcrumbs={[
        { link: '/news/', label: '新着情報' },
        {
          link: frontmatter.slug,
          label: frontmatter.title
        }
      ]}
      title={frontmatter.title}
      description={excerpt}
    >
      <main className='l-main'>
        <div className='p-news-data'>
          <div className='c-date'>
            <time dateTime={frontmatter.date}>{frontmatter.date}</time>
          </div>
          <ul className='c-list c-list--tag'>
            <li>
              <span
                className='c-tag c-tag--category'
                style={{
                  color: '#fff'
                }}
              >
                お知らせ
              </span>
            </li>
          </ul>
        </div>
        <h1 className='c-heading c-heading--article'>{frontmatter.title}</h1>
        <div
          className='wp-wrap-post'
          dangerouslySetInnerHTML={{ __html: html }}
        ></div>
        {0
          ? `
        <div className='c-sns'>
          <ul className='c-sns__list'>
            <li className='c-sns__item'>Twitter</li>
            <li className='c-sns__item'>Facebook</li>
            <li className='c-sns__item'>LINE</li>
          </ul>
        </div>
        `
          : ''}
        <ul className='c-buttons'>
          <li className='c-buttons__item u-align-tac'>
            <Link to='/news/' className='c-buttons__link'>
              <span>一覧へ戻る</span>
            </Link>
          </li>
        </ul>
      </main>
    </Layout>
  );
}
export const pageQuery = graphql`
  query ($id: String!) {
    markdownRemark(id: { eq: $id }) {
      html
      excerpt(pruneLength: 140)
      frontmatter {
        slug
        date(formatString: "YYYY/MM/DD")
        title
      }
    }
  }
`;
